<template>
  <v-container fluid fill-height style="padding:0;" id="webworker" grid-list-md>
    <Blog :items="items" :time="time" :title="title">
      <div class="blogcontent">
        <v-layout row wrap>
          <v-flex sm12>
            <v-hover>
              <v-card
                slot-scope="{ hover }"
                :class="`elevation-${hover ? 12 : 2}`"
                min-height="300"
              >
                <v-card-title>
                  <h3 id="webworker_demo">web-worker</h3>
                </v-card-title>
                <v-divider></v-divider>
                <v-card-text>
                    <p class=".body-1"><code>web-worker</code>是浏览器中worker的一种，实用性的worker还有service-worker</p>
                    <p class=".body-1"><code>web-worker</code>允许我们在单独的线程中运行js，并且可以和主线程相互通信</p>

                    <v-tabs color="light-green darken-1" dark slider-color="yellow">
                        <v-tab key="app" ripple>app.js</v-tab>
                        <v-tab key="worker" ripple>worker.js</v-tab>
                        <v-tab-item key="app">
                            <v-card flat>
                                <v-card-text>
                                    <pre class="code">{{pres[0]}}</pre>
                                </v-card-text>
                            </v-card>
                        </v-tab-item>
                        <v-tab-item key="worker">
                            <v-card flat>
                                <v-card-text>
                                    <pre class="code">{{pres[1]}}</pre>
                                </v-card-text>
                            </v-card>
                        </v-tab-item>
                    </v-tabs>
                </v-card-text>
              </v-card>
            </v-hover>
          </v-flex>
        </v-layout>
      </div>
    </Blog>
  </v-container>
</template>

<script>
import Blog from "../Blog.vue";
export default {
  components: { Blog },
  data() {
    return {
      pres: 
[
    `
    const handleWorker = () => {
        if(window.Worker) {
            const woker = new Worker("worker.js", {name: 'myworker'});

            // 向worker发送消息
            woker.postMessage("form client");

            // 接受worker传递的消息
            woker.onmessage = function(e) {
                console.log(e.data);
            }

            // 处理错误
            woker.onerror = function() { 
                woker.terminate(); // 终结worker
                console.log('worker出错了');
            };
        }
    }

    window.onload = function() {
        handleWorker();
    };
    `,
    `
    console.log(self.name);
    
    // 接受主线程传递的消息
    self.onmessage = (e) => {
        console.log(e.data);

        // 向主线程发送消息
        self.postMessage("from worker");
    };

    // 处理错误
    self.onmessageerror = () => {
        self.close(); // 关闭worker
        console.log('消息反序列化出错');
    };
    `
],
      time: "2019-07-06",
      title: "web-worker",
      items: [
        {
          title: "web-worker",
          target: "#webworker_demo"
        },
        { divider: true },
      ],
    };
  },
  mounted() {
  },
  methods: {}
};
</script>

